<template>
  <div class="timepicker" v-if="timePickerBool">
    <div class="closearea" @click="closeModal()"></div>
    <div class="selectarea">
      <!-- 顶部 -->
      <div class="titlearea clearfix">
        <div class="cancel fl" @click="closeModal()">取消</div>
        <div class="titlename fl">装货时间</div>
        <div class="sub fr" @click="completeSelect()">完成</div>
      </div>

      <!-- 地址区 -->
      <div class="addressarea clearfix">
        <!-- 日期 -->
        <div class="provincearea fl">
          <ul>
            <li
              v-for="option in dateOptions"
              :key="option.date"
              :class="date === option.date ? 'selectedprovince' : 'otherprovince'"
              @click="selectDate(option.date)"
            >{{option.label}}</li>
          </ul>
        </div>
        <!-- 点 -->
        <div class="districtarea fr">
          <ul>
            <li
              v-for="option in timeRangeOptions"
              :key="option.value"
              :class="timeRange === option.value ? 'selecteddistrict' : 'otherdistrict'"
              @click="selectTimeRange(option.value)"
            >{{option.label}}</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";
let date = new Date();



export default {
  data() {
    return {
      date: null,
      timeRange: null,
      dateOptions: [],
      timeRangeOptions: [
        { label: "全天都可以", value: "00:00-00:00" },
        { label: "上午都可以", value: "06:00-12:00" },
        { label: "下午都可以", value: "12:00-18:00" },
        { label: "晚上都可以", value: "18:00-00:00" },
        { label: "凌晨(00:00-01:00)", value: "00:00-01:00" },
        { label: "凌晨(01:00-02:00)", value: "01:00-02:00" },
        { label: "凌晨(02:00-03:00)", value: "02:00-03:00" },
        { label: "凌晨(03:00-04:00)", value: "03:00-04:00" },
        { label: "凌晨(04:00-05:00)", value: "04:00-05:00" },
        { label: "凌晨(05:00-06:00)", value: "05:00-06:00" },
        { label: "上午(06:00-07:00)", value: "06:00-07:00" },
        { label: "上午(07:00-08:00)", value: "07:00-08:00" },
        { label: "上午(08:00-09:00)", value: "08:00-09:00" },
        { label: "上午(09:00-10:00)", value: "09:00-10:00" },
        { label: "上午(10:00-11:00)", value: "10:00-11:00" },
        { label: "上午(11:00-12:00)", value: "11:00-12:00" },
        { label: "下午(12:00-13:00)", value: "12:00-13:00" },
        { label: "下午(13:00-14:00)", value: "13:00-14:00" },
        { label: "下午(14:00-15:00)", value: "14:00-15:00" },
        { label: "下午(15:00-16:00)", value: "15:00-16:00" },
        { label: "下午(16:00-17:00)", value: "16:00-17:00" },
        { label: "下午(17:00-18:00)", value: "17:00-18:00" },
        { label: "晚上(18:00-19:00)", value: "18:00-19:00" },
        { label: "晚上(19:00-20:00)", value: "19:00-20:00" },
        { label: "晚上(20:00-21:00)", value: "20:00-21:00" },
        { label: "晚上(21:00-22:00)", value: "21:00-22:00" },
        { label: "晚上(22:00-23:00)", value: "22:00-23:00" },
        { label: "晚上(23:00-00:00)", value: "23:00-00:00" }
      ],
      timePickerBool: false,
    };
  },
  created() {
    this.dateOptions = [
      { 
        label: "今天",
        date: dayjs().startOf("day").format("YYYY-MM-DD")
      },
      { 
        label: "明天",
        date: dayjs().startOf("day").add(1, "day").format("YYYY-MM-DD")
      },
      { 
        label: dayjs().startOf("day").add(2, "day").format("MM月DD日"),
        date: dayjs().startOf("day").add(2, "day").format("YYYY-MM-DD")
      },
      { 
        label: dayjs().startOf("day").add(3, "day").format("MM月DD日"),
        date: dayjs().startOf("day").add(3, "day").format("YYYY-MM-DD")
      },
    ];
  },
  mounted() {
  },
  methods: {
    closeModal() {
      this.date = null;
      this.timeRange = null;
      this.timePickerBool = false;
    },
    completeSelect() {
      this.timePickerBool = false;
      this.$emit("close", { date: this.date, timeRange: this.timeRange });
    },
    selectDate(date) {
      this.date = date;
    },
    selectTimeRange(timeRange) {
      this.timeRange = timeRange;
    }
  }
};
</script>

